<template>
	<view>
		<scroll-view scroll-y="true" :style="'height:' + height + 'px;'" style="width: 100%;background-color: #f5f6f7;"
			class="column" :refresher-triggered="refresherTriggered"  @scrolltolower="scrolltolower"  @refresherrefresh="refresherrefresh" :refresher-threshold="45" :refresher-enabled="true">
			<view class=""></view>
			<view v-for="(item,index) in listArray" :key="index" class="cell" @click="gotoCell(item)">
				<!-- <view class="contentView"> -->
					<view class="timeview">{{item.addtime}}</view>
					<view class="titleView" style="flex:1;">
						<view class="title1" style="color: #181818;font-size: 18px;">{{item.title}}</view>
						<view class="title1" style="color: #6F6F6F;font-size: 14px;height: 20px;">{{item.content}}</view>
						<view class="bottom row">
							<view class="title2">{{i18n.message.checkdetail}}</view>
							<image class="rightimg" src="../../static/ads-seld-right.png" mode="aspectFit"></image>
						</view>
					</view>
				<!-- </view> -->
			</view>
		</scroll-view>
	</view>
</template>

<script>
	export default {
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		data() {
			return {
				page: 1,
				refresherTriggered: false,
				listArray: [],
				height: 0
			}
		},
		onLoad() {
			uni.setNavigationBarTitle({
				title:this.i18n.message.sysTongzhi
			})
			const res = uni.getSystemInfoSync();
			var top = 44;
			if (parseInt(res.safeArea.top) > 44) {
				top = parseInt(res.safeArea.top)
			}
			this.height = res.windowHeight
			this.requestData()
		},
		methods: {
			gotoCell(item) {
				item.is_read = 1
				uni.navigateTo({
					url:'sysMsgDetail?id='+item.id
				})
			},
			requestData() {
				var that = this
				that.http.sendRequest('Message.Index', {
					'p': that.page
				}).then(function(res) {
					if (res.code == 0) {
						const info2 = res.info[0]
						if (that.page == 1) {
							that.listArray = info2
						} else {
							 that.listArray = that.listArray.concat(info2)
						}
					}
				})
			},
			scrolltolower() {
				console.log('滚动到底部');
				var that = this
				that.page++
				that.requestData()
			},
			refresherrefresh() {
				var that = this
				that.page = 1

				that.refresherTriggered = true;
				that.requestData()
				console.log('自定义下拉刷新被触发');
				setTimeout(() => {
					that.refresherTriggered = false;
				}, 1000);
			},
		},
	}
</script>

<style>
	page{
		width: 100%;
		height: 100%;
		overflow: hidden;
		background-color: #F5F5F5;
	}
	.timeview{
		background-color: #DBDBDB;
		height: 22px;
		margin-top: 15px;
		display: flex;
		flex-direction: row;
		border-radius: 11px;
		color: white;
		align-items: center;
		justify-content: center;
		font-size: 11px;
		width: 50%;
		margin-left: 25%;
	}
	.title1{
		margin-top: 10px;
		margin-left:10px ;
		margin-right: 10px;
		color: #AAA;
		font-size: 12px;
		height: 25px;
		overflow: hidden;
		word-break: break-all;  /* break-all(允许在单词内换行。) */
		text-overflow: ellipsis;  /* 超出部分省略号 */
		display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
		-webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
		-webkit-line-clamp: 1; /** 显示的行数 **/
	}
	
	.titleView{
		background-color: white;
		height: 120px;
		margin-top: 10px;
		margin-left: 15px;
		margin-right: 15px;
		border-radius: 2px;
		display: flex;
		flex-direction: column;
	}
	.bottom{
		margin-top: 10px;
		height:40px ;
		border-top: 1px solid #e6e6e6;
		align-items: center;
		display: flex;
		flex-direction: row;
	}
	.title2{
		flex: 1;
		margin-right: 20px;
		margin-left: 10px;
		font-size: 13px;
		text-align: left;
		color: #6f6f6f;
		
	}
	.cell {
		width: 100%;
		height: 167px;
		display: flex;
		flex-direction: column;
	}

	.rightimg {
		margin-right: 15px;
		/* margin-top: 13px; */
		width: 20px;
		height: 20px;
	}

	.contentView {
		margin-top: 13px;
		margin-left: 10px;
		width: calc(100% - 80px);
		height: 140px;
		display: flex;
		flex-direction: column;
		background-color: red;
	}

	.redBage {
		margin-top: 5px;
		margin-right: 0;
		width: 6px;
		height: 6px;
		border-radius: 3px;
		background-color: #FF4C4C;
	}
</style>
